#hplogo canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('data:image/png;base64,') no-repeat;
  background-size: 80%;
  background-position: center;
  outline: none;
  user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none
}


